.autocomplete {
    transition: transform .6s,
}

.autocomplete:hover {
    transform: scale(1.2);
    transition: transform .6s,
}

.centrar {
    text-align: center;
    margin-left: auto;
    margin-right: auto;
}

.borde {
    border: 2px solid black;
}

.btnSearch {
    border-radius: 5px;
    background-color: white;
    display: inline-block;
}

.inputSearch {
    border-radius: 5px;
    display: inline-block;
}

.weatherLocationAddFormContainer {
    width: 400px;
    text-align: right;
}

.weatherDataContainer {
    width: 400px;
    height: 100px;
    line-height: 100px;
    background-color: rgba(200, 200, 200, 0.5);
    border-radius: 5px;
}

.weatherExtraInfoContainer {
    width: 39%;
    display: inline-block;
    line-height: 28px;
    text-align: left;
}

.weatherTemperatureContainer {
    width: 59%;
    display: inline-block;
}

.temperature {
    font-size: 30px;
    font-weight: bold;
    padding-left: 20px;
    color: #333;
}

.temperatureType {
    font-weight: bold;
    color: #666;
}

.extraInfoText {
    display: block;
    color: #1535C5;
    font-size: 16px;
    font-weight: bold;
}

.wicon {
    color: rgb(255, 72, 0);
    font-size: 25px;
}

.locationContainer {
    width: 100%;
    height: 35px;
    text-align: left;
    padding-left: 10px;
    color: #888;
    line-height: 35px;
}

.locationContainer h1 {
    margin: 0;
}

.weatherLocationContainer {
    width: 400px;
    padding: 15px;
    box-shadow: 10px 10px 40px -12px rgba(0, 0, 0, 0.75);
}